import React from "react";

export default function Social() {
  return (
    <div className="social">
      <div>
        <box-icon
          name="messenger"
          type="logo"
          color="#2e279d"
          size="md"
        ></box-icon>
      </div>
      <div>
        <box-icon
          name="whatsapp"
          type="logo"
          color="green"
          size="md"
        ></box-icon>
      </div>
      <div>
        <box-icon name="twitter" type="logo" color="#1ee3cf" size="md"></box-icon>
      </div>
      <div>
        <box-icon
          name="instagram"
          type="logo"
          color="#5bd1d7"
          size="md"
        ></box-icon>
      </div>
      <div>
        <box-icon name="youtube" type="logo" color="red" size="md"></box-icon>
      </div>
    </div>
  );
}
